/* eslint-disable react/jsx-boolean-value */
/* eslint-disable object-curly-newline */
/* eslint-disable import/no-unresolved */
/* eslint-disable no-unused-vars */
/* eslint-disable react/jsx-indent-props */
/* eslint-disable max-len */
/* eslint-disable quotes */
/* eslint-disable react/jsx-curly-brace-presence */
/* eslint-disable react-hooks/rules-of-hooks */
/* eslint-disable react/jsx-indent */
/* eslint-disable arrow-body-style */
import { Card, Col, DatePicker, Row, Typography } from 'antd';
import dayjs from 'dayjs';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import CurrencyFormat from 'react-currency-format';
import { useDispatch, useSelector } from 'react-redux';
import Cookies from 'universal-cookie';
import ThongKeApi from '../../../api/ThongKeApi';
import { changeDataArrCost } from '../../../redux/actions/arrCostActions';
import ExportExcel from '../ExportExcel';
import ShareCost from './ShareCost';

const index = (props) => {
  // ? state extension
  const cookies = new Cookies();
  const idchuhang = useSelector((state) => state.idchuhang);
  const arrCost = useSelector((state) => state.arrCost);
  const { Title } = Typography;
  const dispatch = useDispatch();
  const format = 'MM/YYYY';
  const formatMMYYYY = 'MM-YYYY';
  const monthCurrent = moment().format(format);
  const monthMMYYYY = moment().format(formatMMYYYY);
  const [date, setDate] = useState(monthCurrent);
  // ? state component
  const [datatotal, setdatatotal] = useState('');
  const [dataStogareProduct, setDataStogareProduct] = useState([]);
  const [unLoadingProduct, setUnLoadingProduct] = useState([]);
  const [incurredProduct, setincurredProduct] = useState([]);
  const [exportProduct, setExportProduct] = useState([]);
  const [handleProduct, setHandleProduct] = useState([]);
  const [handleProductReturn, setHandleProductReturn] = useState([]);
  const [handleDC, setHandleDC] = useState([]);
  const [packProduct, setPackProduct] = useState([]);

  //* CALL API
  useEffect(() => {
    if (arrCost.arrCost.length === 0) {
      const fetchCategoryProduct = async () => {
        const month = parseInt(date.split('/')[0], 10);
        const year = parseInt(date.split('/')[1], 10);
        let startOfMonth = moment()
          .month(month - 1)
          .year(year)
          .startOf('month')
          .format('YYYY-MM-DD');
        let endOfMonth = moment()
          .month(month - 1)
          .year(year)
          .endOf('month')
          .format('YYYY-MM-DD');
        try {
          const data = {
            date_From: startOfMonth,
            date_To: endOfMonth,
            chu_Hang_ID: cookies.get('idchuhang'),
            kho_ID: 2631604,
          };

          const response = await ThongKeApi.getAllChiPhiVanHanh(data);
          setdatatotal(response);
          console.log(arrCost.arrCost);
          if (arrCost.arrCost.length === 0) {
            dispatch(
              changeDataArrCost({
                arrCost: [
                  response.phiLuuKho_ThanhTien,
                  response.phiXuLyHangHoa_ThanhTien,
                  response.phiBocXep_ThanhTien,
                  response.phiSoanDongGoi_ThanhTien,
                  response.phiVatTuBaoBi_ThanhTien,
                  response.phiXuLyDC_ThanhTien,
                  response.phiXuLyHangHoan_ThanhTien,
                  response.phiXuatHang_ThanhTien,
                  {
                    detail: [...response.detail],
                  },
                  response.tongCong,
                ],
              })
            );
          } else {
            dispatch(changeDataArrCost({ arrCost: [] }));
          }

          const mapDataStorageProduct = response.detail.filter(
            (item) => item.loai === 'L1'
          );
          const mapUnloading = response.detail.filter(
            (item) => item.loai === 'L2'
          );
          const mapincurredProduct = response.detail.filter(
            (item) => item.loai === 'L6'
          );
          const mapExportProduct = response.detail.filter(
            (item) => item.loai === 'L5'
          );
          const mapHandleProduct = response.detail.filter(
            (item) => item.loai === 'L3'
          );
          const mapHandleProductReturn = response.detail.filter(
            (item) => item.loai === 'L8'
          );
          const mapHandleDC = response.detail.filter(
            (item) => item.loai === 'L7'
          );

          const mapPackProduct = response.detail.filter(
            (item) => item.loai === 'L4'
          );

          setDataStogareProduct(mapDataStorageProduct);
          setUnLoadingProduct(mapUnloading);
          setincurredProduct(mapincurredProduct);
          setExportProduct(mapExportProduct);
          setHandleProduct(mapHandleProduct);
          setHandleProductReturn(mapHandleProductReturn);
          setHandleDC(mapHandleDC);
          setPackProduct(mapPackProduct);
        } catch (error) {
          console.log('fail to fetch list item', error);
        }
      };
      fetchCategoryProduct();
    } else {
      console.log(arrCost.arrCost);
      setDataStogareProduct(
        arrCost.arrCost[8].detail.filter((item) => item.loai === 'L1')
      );
      setUnLoadingProduct(
        arrCost.arrCost[8].detail.filter((item) => item.loai === 'L2')
      );
      setincurredProduct(
        arrCost.arrCost[8].detail.filter((item) => item.loai === 'L6')
      );
      setExportProduct(
        arrCost.arrCost[8].detail.filter((item) => item.loai === 'L5')
      );
      setHandleProduct(
        arrCost.arrCost[8].detail.filter((item) => item.loai === 'L3')
      );
      setHandleProductReturn(
        arrCost.arrCost[8].detail.filter((item) => item.loai === 'L8')
      );
      setHandleDC(
        arrCost.arrCost[8].detail.filter((item) => item.loai === 'L7')
      );
      setPackProduct(
        arrCost.arrCost[8].detail.filter((item) => item.loai === 'L4')
      );
    }

    return () => {
      setdatatotal('');
      setDataStogareProduct([]);
      setUnLoadingProduct([]);
      setincurredProduct([]);
      setExportProduct([]);
      setHandleProduct([]);
      setHandleProductReturn([]);
      setHandleDC([]);
      setPackProduct([]);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [idchuhang.idchuhang, date]);

  //* RENDER COMPONENT
  return (
    <Col span={24}>
      <div
        style={{
          width: '100%',
          marginBottom: '2px',
          alignItems: 'center',
        }}
      >
        <Title
          level={5}
          style={{
            padding: '2px 5px',
            color: 'black',
            fontWeight: 'normal',
            // backgroundColor: '#d63031',
            marginBottom: '0px',
            display: 'inline-block',
          }}
        >
          {`Tng chi ph vn hnh thng `}{' '}
          <DatePicker
            className="createDateRangePicker"
            popupClassName="createDateRangePicker"
            picker="month"
            format={'MM/YYYY'}
            defaultValue={
              localStorage.getItem('CostMonth')
                ? dayjs(localStorage.getItem('CostMonth'), format)
                : dayjs(monthCurrent, format)
            }
            onChange={(value, dateString) => {
              setDate(dateString);
              dispatch(changeDataArrCost({ arrCost: [] }));
              localStorage.setItem('CostMonth', dateString);
            }}
          />
          <CurrencyFormat
            value={Number(
              datatotal.tongCong
                ? datatotal.tongCong.toFixed(0)
                : arrCost.arrCost[9] || 0
            )}
            displayType={'text'}
            thousandSeparator={true}
            renderText={(value) => (
              <span className="badge_custom">
                {value} {``}
              </span>
            )}
          />
          <ExportExcel
            filename={`Tong_chi_phi_van_hanh_${parseInt(
              date.split('/')[0],
              10
            )}-${parseInt(date.split('/')[1], 10)}`}
            date={date}
          />
        </Title>
      </div>

      <Card
        style={{
          backgroundColor: 'white',
          border: '1px solid #b2bec3',
          borderTop: '3px solid #ff3838',
          marginBottom: '10px',
          borderRadius: '5px',
        }}
      >
        <Row gutter={16}>
          <ShareCost
            total={
              arrCost
                ? parseInt(arrCost.arrCost[0], 10)
                : datatotal.phiLuuKho_ThanhTien
            }
            title="Ph lu kho"
            data={dataStogareProduct}
          />
          <ShareCost
            total={
              arrCost
                ? parseInt(arrCost.arrCost[1], 10)
                : datatotal.phiXuLyHangHoa_ThanhTien
            }
            title="Ph x l hng ha"
            data={handleProduct}
          />
          <ShareCost
            total={
              arrCost
                ? parseInt(arrCost.arrCost[2], 10)
                : datatotal.phiBocXep_ThanhTien
            }
            title="Ph bc xp hng ha"
            data={unLoadingProduct}
          />
          <ShareCost
            total={
              arrCost
                ? parseInt(arrCost.arrCost[3], 10)
                : datatotal.phiSoanDongGoi_ThanhTien
            }
            title="Ph son hng v ng gi"
            data={packProduct}
          />
          <ShareCost
            total={
              arrCost
                ? parseInt(arrCost.arrCost[4], 10)
                : datatotal.phiVatTuBaoBi_ThanhTien
            }
            title="Ph vt t bao b"
            data={incurredProduct}
          />
          <ShareCost
            total={
              arrCost
                ? parseInt(arrCost.arrCost[5], 10)
                : datatotal.phiXuLyDC_ThanhTien
            }
            title="Ph x l DC"
            data={handleDC}
          />
          <ShareCost
            total={
              arrCost
                ? parseInt(arrCost.arrCost[6], 10)
                : datatotal.phiXuLyHangHoan_ThanhTien
            }
            title="Ph x l hng hon"
            data={handleProductReturn}
          />
          <ShareCost
            total={
              arrCost
                ? parseInt(arrCost.arrCost[7], 10)
                : datatotal.phiXuatHang_ThanhTien
            }
            title="Ph hon tt n hng"
            data={exportProduct}
          />
        </Row>
      </Card>
    </Col>
  );
};

export default index;
